
import { useEffect, useState } from 'react';
import './App.css';
import {db} from './firebase';
import {collection, getDocs, addDoc,updateDoc, doc, deleteDoc} from 'firebase/firestore';
function App() {
    const [newname, setnewname]= useState("")
    const [newoccasion, setnewoccasion]=useState("")
    const [newphoneno, setnewphoneno]=useState(0)

    const [users, setUsers] = useState([]);
    const usercollectionref= collection(db,"table");

    const deleteuser = async (id) => {
      const current=doc(db,"table",id)
      await deleteDoc(current);
      window.location.reload();
    }

    const updateuserage = async (id) => {
      const current=doc(db,"table",id)
      await updateDoc(current, {occasion: newoccasion});
      window.location.reload();
    }
    const updateuserphoneno = async (id) => {
      const current=doc(db,"table",id)
      await updateDoc(current, {phoneno: newphoneno});
      window.location.reload();
    }
    const updateusername = async (id) => {
      const current=doc(db,"table",id)
      await updateDoc(current, {Name: newname});
      window.location.reload();
    }
    const createuser =  async () => {
      await addDoc(usercollectionref, {Name: newname, occasion: newoccasion, phoneno: newphoneno});
      window.location.reload();
    }
    useEffect(() => {
      const getUsers= async () => {
          const data = await getDocs(usercollectionref);
          setUsers(data.docs.map((doc) => ({...doc.data(),id: doc.id})));
      };
      getUsers();
    },
    []);



   return( <div className="App">
     <h1>HMS PhotoGraphy Booking</h1>
     <input placeholder=" name " onChange={(event) => {setnewname(event.target.value);}}/>
     <input placeholder="occasion  "onChange={(event) => {setnewoccasion(event.target.value);}}/>
     <input type= "tel" name="telephone" title="XXXXXXXXXX" required pattern="[0-9] {10}" placeholder="8888888888"onChange={(event) => {setnewphoneno(event.target.value);}}/>
     <button onClick={createuser}>Book Now!</button>
     {users.map((user) => {
       return (
         <div>
           {" "}
           <h1> Name: {user.Name}</h1>
           <h1>occasion: {user.occasion}</h1>
           <h1>phoneno: {user.phoneno}</h1>
           <input placeholder=" edit name " onChange={(event) => {setnewname(event.target.value);}}/>
           <button onClick={() => { 
             updateusername(user.id)}}>edit name</button>
           <input placeholder=" edit occasion " onChange={(event) => {setnewoccasion(event.target.value);}}/>
           <button onClick={() => { 
             updateuserage(user.id)}}>edit occasion</button>
           <input placeholder=" edit phoneno " onChange={(event) => {setnewphoneno(event.target.value);}}/>
           <button onClick={() => { 
             updateuserage(user.id)}}>edit phoneno</button>
           <button onClick={() => {
             deleteuser(user.id)}}>delete</button>
        </div>
       );
     })}
   </div>
  );
}

export default App;
